.login-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgb(44 6 85 / 56%);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999999990;
}

.login-modal {
  background: #ffffff;
  padding: 2.5rem;
  border-radius: 0.75rem;
  width: 100%;
  max-width: 400px;
  position: relative;
  border: 2px solid var(--primary-purple);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.4);
}

h2 {
  margin: 0;
  color: var(--primary-purple);
  font-size: 1.75rem;
  text-align: center;
}

.login-subtitle {
  color: var(--primary-blue);
  text-align: center;
  margin: 1rem 0 2rem;
  font-size: 0.9rem;
}

.error-message {
  background: rgba(231, 76, 60, 0.1);
  color: #e74c3c;
  padding: 0.75rem;
  border-radius: 0.25rem;
  margin-bottom: 1.5rem;
  font-size: 0.875rem;
  border: 1px solid rgba(231, 76, 60, 0.2);
}

.form-group {
  margin-bottom: 1.5rem;
}

label {
  display: block;
  margin-bottom: 0.5rem;
  color: var(--text-dark);
  font-weight: 500;
  font-size: 0.9rem;
}

input {
  width: 100%;
  padding: 0.75rem;
  background: var(--panel-bg);
  border: 2px solid var(--primary-purple) !important;
  border-radius: 0.25rem;
  font-size: 1rem;
  color: var(--primary-blue);
  transition: all 0.2s;
}

input::placeholder {
  color: #4a154b;
}

input:focus {
  outline: none;
  border-color: var(--primary-blue);
  box-shadow: 0 0 0 2px rgba(255, 190, 11, 0.2);
}

.login-submit {
  width: 100%;
  padding: 0.875rem;
  border-radius: 0.25rem;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  margin-top: 1.5rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  transition: all 0.2s;
  background: var(--panel-bg);
  color: var(--primary-purple);
  border: 2px solid var(--primary-purple);
}

.login-submit:hover {
  background: var(--primary-purple);
  color: #ffffff;
  transform: translateY(-1px);
  box-shadow: 0 4px 8px rgba(151, 71, 255, 0.18);
}

.login-submit:active {
  transform: translateY(0);
}