.header {
    text-align: center;
    padding: 20px;
    background: linear-gradient(to right, #9747ff, #155eef);
    color: white;
    border-radius: 8px;
    width: -webkit-fill-available;
    margin: 40px 0;
}

.header-logo h1 {
    margin: 0;
    font-size: 2.5rem;
    color: #fff;
}

.header-logo p {
    margin: 0.25rem 0 0;
    font-size: 1.875rem;
    color: #fff;
}

.header-actions {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  position: absolute;
  top: 85px;
  right: 30px;
}

.icon-button {
  background: linear-gradient(to right, #9747ff, #155eef);
  border: 2px solid #9747ff;
  font-size: 1.5rem;
  padding: 0.5rem;
  cursor: pointer;
  position: relative;
  display: flex;
  align-items: center;
  color: #fff;
  transition: color 0.2s;
  border-radius: 30px;
}

.icon-button:hover {
  background: transparent;
  color: #155eef;
}

.badge {
  position: absolute;
  top: -5px;
  right: -5px;
  background: #ffbe0b;
  color: #1a1a1a;
  font-size: 0.75rem;
  padding: 0.125rem 0.375rem;
  border-radius: 1rem;
  min-width: 1.25rem;
  text-align: center;
  font-weight: bold;
}

.user-profile {
  position: relative;
}

.user-name {
  font-size: 0.875rem;
  margin-left: 0.5rem;
  color: inherit;
}

.dropdown {
  position: absolute;
  top: 100%;
  right: 0;
  background: #1a1a1a;
  border: 1px solid #333;
  border-radius: 0.5rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
  padding: 0.5rem;
  margin-top: 0.5rem;
}

.dropdown button {
  background: none;
  border: none;
  color: #fff;
  padding: 0.5rem 1rem;
  width: 100%;
  text-align: left;
  cursor: pointer;
  border-radius: 0.25rem;
}

.dropdown button:hover {
  background: #333;
}

.user-profile:hover .dropdown {
  display: block;
}

.login-button {
  background: #ffbe0b;
  color: #1a1a1a;
  border: none;
  padding: 0.5rem 1.5rem;
  border-radius: 0.25rem;
  cursor: pointer;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  font-size: 0.875rem;
  transition: all 0.2s;
}

.login-button:hover {
  background: #ffd60a;
  transform: translateY(-1px);
}

.login-button:active {
  transform: translateY(0);
}