.api-setup-container {
    max-width: 500px;
    margin: 2rem auto;
    padding: 2.5rem;
    border: 2px solid #e6dbff;
    border-radius: 15px;
    box-shadow: 0 4px 12px rgba(151, 71, 255, 0.1);
    background: #ffffff;
    min-height: calc(100vh - 4rem);
}

.api-setup-container h2 {
    margin-bottom: 1.5rem;
    font-size: 2rem;
    color: #4a154b;
    font-weight: 600;
}

.api-setup-field {
    margin-bottom: 1.5rem;
}

.api-setup-field label {
    font-weight: 500;
    color: #4a154b;
    display: block;
    margin-bottom: 0.5rem;
    font-size: 1rem;
}

.api-setup-field input {
    width: 100%;
    padding: 0.75rem;
    margin-top: 0.25rem;
    border: 2px solid #e6dbff;
    color: #4a154b;
    border-radius: 8px;
    font-size: 1rem;
    box-sizing: border-box;
    background: #ffffff;
    transition: all 0.3s ease;
}

.api-setup-field input:focus {
    outline: none;
    border-color: #9747FF;
    box-shadow: 0 0 0 3px rgba(151, 71, 255, 0.2);
}

.api-setup-btn {
    padding: 0.75rem 1.5rem;
    background: #9747FF;
    color: #ffffff;
    border: none;
    border-radius: 8px;
    font-size: 1rem;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
    width: 100%;
}

.api-setup-btn:hover {
    background: #8033FF;
    transform: translateY(-1px);
    box-shadow: 0 4px 8px rgba(151, 71, 255, 0.2);
}

.api-setup-success {
	color: green;
	margin-top: 12px;
	font-weight: 500;
}
