.App {
  text-align: center;
  min-height: 100vh;
  background: #f8f5ff;
}

:root {
  --primary-purple: #9747FF;
  --primary-blue: #155EEF;
  --text-dark: #0f1b4d;
  --muted-blue: #9fb8ff;
  --muted-bg: #f3f0ff;
  --panel-bg: #ffffff;
}

.main-nav {
  background: #ffffff;
  border-bottom: 1px solid #e6dbff;
  padding: 15px;
  box-shadow: 0 2px 8px rgba(151, 71, 255, 0.1);
}

.nav-list {
  display: flex;
  justify-content: center;
  gap: 24px;
  list-style: none;
  margin: 0;
  padding: 0;
}

.nav-btn {
  background: var(--panel-bg);
  color: var(--primary-purple);
  border: 2px solid var(--primary-purple);
  border-radius: 8px;
  padding: 10px 24px;
  font-weight: 600;
  cursor: pointer;
  font-size: 16px;
  transition: all 0.3s ease;
}

.nav-btn.active,
.nav-btn:hover {
  background: var(--primary-purple);
  color: #ffffff;
  transform: translateY(-1px);
  box-shadow: 0 4px 8px rgba(151, 71, 255, 0.18);
}

.main-content {
  min-height: 80vh;
  margin: 0 auto;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
