.qa-page {
  max-width: 900px;
  margin: 24px auto;
  padding: 16px;
  font-family: Arial, Helvetica, sans-serif;
}

.qa-page h1 {
  margin-bottom: 12px;
}

.rec-area {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 18px;
}

.rec-btn {
  padding: 8px 14px;
  background: #007bff;
  color: #fff;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}

.rec-btn.recording {
  background: #c62828;
}

.rec-status {
  color: #333;
  font-weight: 500;
}

.accordion {
  border-top: 1px solid #e0e0e0;
}

.accordion-item {
  border-bottom: 1px solid #eaeaea;
}

.accordion-title {
  width: 100%;
  text-align: left;
  padding: 14px 12px;
  background: #1a1a1a;
  border: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 16px;
  cursor: pointer;
}

.accordion-title.open {
  background: #9747FF;
}

.chev {
  font-weight: 700;
  margin-left: 12px;
}

.accordion-content {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.2s ease, padding 0.2s ease;
  padding: 0 12px;
}

.accordion-content.open {
  padding: 12px;
  max-height: 240px;
}

.accordion-content p {
  margin: 0;
  color: #333;
}
